<template>
  <section>
    <div class="operate">
      <div class="tips">tips</div>
      <div class="mapPosition">{{ history }}</div>
    </div>
    <div id="main" ref="echartsMap">
    </div>
    <div class="desc">json</div>
  </section>

</template>
<script>
import * as echarts from 'echarts'
import CHINAJSON from './data/china.json' //
import { regionData as hebeiRegionData, DATAJSON as hebei } from './data/(130000).js' //
import JSON130110 from './data/130110.geojson' //-
import JSON130132 from './data/130132.geojson' //-
import JSON_SHANGZHUANG from './data/.geojson' //--
export default {
  data() {
    return {
      myCharts: null, //echarts
      history: [{
        name: '',
        level: 1
      }],
      level: 1,// 1 2 3 4 5 6
      dataList: [
        { ename: "beijing", name: "" },
        { ename: "tianjin", name: "" },
        { ename: "shanghai", name: "" },
        { ename: "chongqing", name: "" },
        { ename: "hebei", name: "", dataJson: hebei },
        { ename: "henan", name: "" },
        { ename: "yunnan", name: "" },
        { ename: "liaoning", name: "" },
        { ename: "heilongjiang", name: "" },
        { ename: "hunan", name: "" },
        { ename: "anhui", name: "" },
        { ename: "shandong", name: "" },
        { ename: "xinjiang", name: "" },
        { ename: "jiangsu", name: "" },
        { ename: "zhejiang", name: "" },
        { ename: "jiangxi", name: "" },
        { ename: "hubei", name: "" },
        { ename: "guangxi", name: "" },
        { ename: "gansu", name: "" },
        { ename: "shanxi", name: "" },
        { ename: "neimenggu", name: "" },
        { ename: "shanxi1", name: "" },
        { ename: "jilin", name: "" },
        { ename: "fujian", name: "" },
        { ename: "guizhou", name: "" },
        { ename: "guangdong", name: "" },
        { ename: "qinghai", name: "" },
        { ename: "xizang", name: "" },
        { ename: "sichuan", name: "" },
        { ename: "ningxia", name: "" },
        { ename: "hainan", name: "" },
        { name: "" },
        { ename: "xianggang", name: "" },
        { ename: "aomen", name: "" },
        { name: "" },
      ],
      // 
      // echarts
      mapOption: {
        title: {
          text: ['{a|}', '{b|}'].join(""),
          top: 75,
          left: 'center',
          textStyle: {
            fontSize: 24,
            rich: {
              a: {
                fontSize: 24,
                color: 'rgba(255,255,255,1)',
              },
              b: {
                fontSize: 24,
                fontWeight: 'lighter',
                color: 'rgba(255,255,255,1)',
              },
            },
          },
          subtextStyle: {//
            color: 'rgba(255,255,255,.4)',
          },
        },

        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow',
          },
        },
        animationDuration: 1000,
        animationEasing: 'cubicOut',
        animationDurationUpdate: 1000,
        backgroundColor: '', // 
        geo: {
          name: 'map',
          map: 'echartsMap', // 
          roam: false, //
          zoom: 1,
          layoutSize: '80%',
          layoutCenter: ['50%', '50%'],
          aspectScale: 0.7, //
          label: {
            show: true,
            color: 'rgba(255, 255, 255, 0.5)',
            emphasis: {
              color: '#fff',
            },
          },
          itemStyle: {
            areaColor: '#226DA4',
            borderColor: 'rgba(255, 255, 255, 0.5)',
            //
            shadowColor: '#6986c1',
            shadowOffsetY: 5,
            shadowOffsetX: 5,
          },

          regions: [], //
        },

        series: [
          {
            type: 'map',
            name: 'echartsMap',
            mapType: 'echartsMap',
            roam: false,
            selectedMode: false, //
            zoom: 1,
            layoutSize: '80%',
            layoutCenter: ['50%', '50%'],
            aspectScale: 0.7, //
            label: {
              show: true,
              color: '#fff',
            },
            // 
            itemStyle: {
              areaColor: '#2860a4',
              borderColor: '#6baaf0',
              borderType: 'solid',
              // shadowColor: '#6fafed',
              // shadowBlur: 15,
              borderWidth: 2,
            },

            // 
            emphasis: {
              show: true,
              label: {
                show: true,
                color: '#fff',
              },
              itemStyle: {
                opacity: 1,
                areaColor: '#0b7571',
                borderColor: '#099799',
                borderType: 'solid',
                shadowColor: '#099799',
                shadowBlur: 15,
                borderWidth: 2,
              },
            },
            select: {
              color: '#fff',
              itemStyle: {
                areaColor: '#00c9ff',
                color: '#fff',
                borderColor: '#00deff',
                borderType: 'solid',
                shadowColor: '#5de5f3',
                shadowBlur: 2,
              },
            },
            tooltip: {
              trigger: 'item',
              backgroundColor: '#fff',
              borderColor: 'transparent',
              extraCssText: 'z-index:100;color:#fff;',
              confine: true, // tooltip 
              formatter: (params, ticket, callback) => {
                //
                let res = ''
                // 
                const testNum = Math.floor(Math.random() * 10)
                res = `<div style="box-shadow: 0 0 10px #3BD9D9; padding: 10px; min-width:100px;position: absolute; top: 0; left:0;  border-radius: 4px; border: 1px solid #04b9ff; background: linear-gradient(to bottom,  #131d45 0%,rgba(35,90,178,.8) 100%);">
                             <div style='color:#fff; font-size: 14px;'>${params.name}</div>

                            <div style='color:#ccc;font-size: 12px;background:rgba(0,0,0.8);padding:0 6px;margin-top:5px'>${testNum
                  }</div>
                         `
                return res
              },
            },
            data: [],
          },
          {
            name: '',
            type: 'effectScatter',//
            // type: 'scatter',//
            coordinateSystem: 'geo',
            data: [{
              value: [],
              itemStyle: {
                color: '#fff',
                shadowBlur: 1,
                shadowColor: '#EEE'
              },
              label: { // 
                position: 'top',
              }
            }],
            symbolSize: 8,
            encode: {
              value: 20
            },
            showEffectOn: 'render',
            rippleEffect: {
              brushType: 'stroke'
            },
            emphasis: {
              scale: true
            },
            zlevel: 1
          },
        ],
      },
    }
  },
  created() {
    /**
     * 
     */
    this.loadMap('')
  },
  methods: {
    loadMap(name) {
      /**
      * 
      */
      this.$nextTick(() => {
        switch (this.level) {
          case 1:
            // 
            echarts.registerMap('echartsMap', CHINAJSON)
            break;
          case 2:
            const provinceData = this.dataList.find(item => item.name == name)
            if (!provinceData) { alert(''); this.returnUpLevel(); return; }
            echarts.registerMap('echartsMap', this.dataList.find(item => item.name == name).dataJson)
            break;
          case 3:
            hebei && hebei.features.forEach((val, i) => {
              if (name.indexOf(val.properties.name) > -1) {
                echarts.registerMap('echartsMap', hebeiRegionData[`Json${val.properties.adcode}`])
              }
            })
            break;
          case 4:
            const regionEnum = {
              '': JSON130110,
              '': JSON130132
            }
            if (!regionEnum[name]) {
              alert(''); this.returnUpLevel(); return;
            }
            echarts.registerMap('echartsMap', regionEnum[name])
            break;
          case 5:
            const villageEnum = {
              '': JSON_SHANGZHUANG,
              '': JSON130132
            }
            if (!villageEnum[name]) {
              alert(''); this.returnUpLevel(); return;
            }
            echarts.registerMap('echartsMap', villageEnum[name])
            break;
          case 6:
            break;
          default:
            break;
        }
        /**
         * 
         */
        this.loadData(name)
        /**
         * echarts
         * 
         */
        if (!this.myCharts) {
          this.myCharts = echarts.init(document.getElementById('main'))
        }
        this.$nextTick(() => {
          window.onresize = () => {
            //echarts
            this.myCharts.resize()
          }
        })
        /**
         *  
         */
        this.updateOption()
      })
    },
    updateOption() {
      this.myCharts.setOption(this.mapOption)
      /**
       * 
       */
      this.myCharts.off('click')
      this.myCharts.on('click', (param) => {
        console.log('')
        console.log(param)

        /**
        * 
        * 
        */
        if (this.level === 5) {
          this.myCharts.dispatchAction({
            type: 'downplay',
          })
          this.myCharts.dispatchAction({
            type: 'highlight',
            name: param.name,
          })
          if (this.history.length == 6) { this.history.pop() }
          this.history.push({
            name: param.name,
            level: 6
          })
          this.loadData(param.name)
          return
        }
        this.level = this.level + 1

        // 
        this.loadMap(param.name)
        this.history.push({
          name: param.name,
          level: this.level
        })
      })
      // 
      // 
      this.myCharts.getZr().off('dblclick')
      this.myCharts.getZr().on('dblclick', (param) => {
        // 
        this.myCharts.dispatchAction({
          type: 'downplay',
        })
        this.returnUpLevel()
        //  function
      })
    },
    /**
     * 
     * xxxxxx
     */
    loadData(name) {
      console.log('\x1B[36m%s\x1B[0m', '', name)
      // 
      name == '' && (this.mapOption.series[0].data = [{ name: '', value: 222 }], this.mapOption.series[1].data = [{ name: '', value: [114.39162, 38.01365] }])
      if (name == '') {
        this.mapOption.series[0].data = [{ name: '', value: 333 }]
        this.mapOption.series[1].data = [{ name: '', value: [114.39162, 38.01365] }, { name: '', value: [114.69162, 39.01365] }]
      }
      name == '' && (this.mapOption.series[0].data = [{ name: '', value: 4444 }], this.mapOption.series[1].data = [])
    },
    // 
    returnUpLevel() {
      if (this.history.length == 1) { return }
      console.log('')
      /**
       * 
       * 1 
       */
      this.myCharts.dispatchAction({
        type: 'downplay',
      })
      this.history.length !== 6 && (this.level = this.level - 1)
      this.history.pop()
      this.loadMap(this.history[this.history.length - 1].name)
    }
  }
}
</script>

<style scoped>
#main {
  background: #333;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1
}

.operate {
  position: absolute;
  z-index: 2;
  text-align: center;
  color: #fefefe;
  top: 10px;
  width: 100%;
}

.operate .tips {
  display: inline-block;
  padding: 3px 10px;
  color: #999
}

.mapPosition {
  line-height: 30px;
}

.desc {
  text-align: center;
  color: #999;
  position: absolute;
  bottom: 10px;
  width: 100%;
  z-index: 2;
}
</style>